.header {
	padding: 25px 0;
	background-color: #ffffff;
	box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.25);
	margin-bottom: 35px;
	&Dark {
		padding: 25px 0;
		background-color: #b9b9b9;
		box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.25);
		margin-bottom: 35px;
	}
}

.header__row {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header__logo {
	font-weight: 500;
	font-size: 26px;
	color: #24bb96;
}

.header__navbar {
	display: flex;
	align-items: center;
	justify-content: space-around;
	font-family: "Inter", sans-serif;

	li {
		margin-left: 12px;
	}
	a {
		transition: 0.3s;
		&:hover {
			color: #24bb96;
		}
	}
}

//Toggle
.toggleContainer {
	position: relative;
	width: 120px;
}

.toggleWrapper {
	position: absolute;
	top: 50%;
	left: 50%;
	overflow: hidden;
	transform: translate3d(-50%, -50%, 0);

	input {
		position: absolute;
		left: -99em;
	}
}

.toggle {
	cursor: pointer;
	display: inline-block;
	position: relative;
	width: 90px;
	height: 50px;
	background-color: #83d8ff;
	border-radius: 90px - 6;
	transition: background-color 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.toggle__handler {
	display: inline-block;
	position: relative;
	z-index: 1;
	top: 3px;
	left: 3px;
	width: 50px - 6;
	height: 50px - 6;
	background-color: #ffcf96;
	border-radius: 50px;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
	transition: all 400ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
	transform: rotate(-45deg);

	.crater {
		position: absolute;
		background-color: #e8cda5;
		opacity: 0;
		transition: opacity 200ms ease-in-out;
		border-radius: 100%;
	}

	.crater1 {
		top: 18px;
		left: 10px;
		width: 4px;
		height: 4px;
	}

	.crater2 {
		top: 28px;
		left: 22px;
		width: 6px;
		height: 6px;
	}

	.crater3 {
		top: 10px;
		left: 25px;
		width: 8px;
		height: 8px;
	}
}

.star {
	position: absolute;
	background-color: #ffffff;
	transition: all 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
	border-radius: 50%;
}

.star1 {
	top: 10px;
	left: 35px;
	z-index: 0;
	width: 30px;
	height: 3px;
}

.star2 {
	top: 18px;
	left: 28px;
	z-index: 1;
	width: 30px;
	height: 3px;
}

.star3 {
	top: 27px;
	left: 40px;
	z-index: 0;
	width: 30px;
	height: 3px;
}

.star4,
.star5,
.star6 {
	opacity: 0;
	transition: all 300ms 0 cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.star4 {
	top: 16px;
	left: 11px;
	z-index: 0;
	width: 2px;
	height: 2px;
	transform: translate3d(3px, 0, 0);
}

.star5 {
	top: 32px;
	left: 17px;
	z-index: 0;
	width: 3px;
	height: 3px;
	transform: translate3d(3px, 0, 0);
}

.star6 {
	top: 36px;
	left: 28px;
	z-index: 0;
	width: 2px;
	height: 2px;
	transform: translate3d(3px, 0, 0);
}

input:checked {
	+ .toggle {
		background-color: #749dd6;

		.toggle__handler {
			background-color: #ffe5b5;
			transform: translate3d(40px, 0, 0) rotate(0);

			.crater {
				opacity: 1;
			}
		}

		.star1 {
			width: 2px;
			height: 2px;
		}

		.star2 {
			width: 4px;
			height: 4px;
			transform: translate3d(-5px, 0, 0);
		}

		.star3 {
			width: 2px;
			height: 2px;
			transform: translate3d(-7px, 0, 0);
		}

		.star4,
		.star5,
		.star6 {
			opacity: 1;
			transform: translate3d(0, 0, 0);
		}
		.star4 {
			transition: all 300ms 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
		}
		.star5 {
			transition: all 300ms 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
		}
		.star6 {
			transition: all 300ms 400ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
		}
	}
}
//!Toggle
